import { defineComponent } from "vue";
export default defineComponent({
    name: "PushTest",
    data() {
        return {
            pushList: [],
            updatePushList: [],
            timeValue: ''
        };
    },
    async created() {
        let object = {
            name: "아이디입력",
            token: "토큰입력"
        };
        this.pushList.push(object);
    },
    methods: {
        change() {
            let startDate = document.querySelector("#time").value;
            this.timeValue = startDate;
        },
        addDataBtn() {
            //서버로 로그인 데이터 보내기
            let object = {
                name: "아이디입력",
                token: "토큰입력"
            };
            this.pushList.push(object);
        },
        reservationPushBtn() {
            //서버로 시간 보내기
            console.log(this.timeValue);
        },
        //푸시 버튼
        pushAddBtn() {
            this.updatePushList = [];
            this.pushList.forEach((v) => {
                if (v.name !== "아이디입력") {
                    this.updatePushList.push(v);
                }
            });
        }
    }
});
